.invest-details{
	margin-top:40px;
	padding-bottom:60px;
	text-align:center;
	background-color:$white;
	padding-top:10px;
	.project-name{
		font-size:$f13;
		color:#666666;
		line-height: $f15;
		margin-bottom:5px;
	}
	.can-use{
		font-size:$f10;
		color:#ccc;
		margin-bottom:5px;
	}
	.year-rate{
		font-size:$f12;
		margin-bottom:5px;
		color:#999999;
	}
	.year-rate-num{
		font-size:2.8rem;
		color:$red;
		margin-bottom:5px;
		span{
			font-size:$f18;
		}
	}
	.start-base{
		font-size:$f15;
		color:#666;
		margin-bottom:10px;
	}
	.progress{
		margin-bottom:30px;
		.num{
			text-align: right;
		}
		.progress-bar{
			width:100%;
			height:5px;
			border-radius:10px;
			background-color:#999999;
			overflow: hidden;
			margin-bottom:5px;
			.progress-state{
				width:55%;
				height:100%;
				float: left;
				background-color:$yellow;
			}
		}
		.overplus{
			color:$yellow;
		}
	}
	ul{
		width:100%;
		height:auto;
		margin-bottom:10px;
		li{
			margin-bottom:1px;
			text-align: left;
			height:40px;
			line-height: 40px;
			font-size:$f13;
			border-bottom:1px solid #eeeeee;
			color:#999999;
		}
	}
	.rights{
		color:#6d6d6d;
	}
}

.go-invest{
	position:fixed;
	bottom:0;
	left:50%;
	-webkit-transform:translateX(-50%);
	-moz-transform:translateX(-50%);
	-ms-transform:translateX(-50%);
	-o-transform:translateX(-50%);
	transform:translateX(-50%);
	.calc{
		height: 45px;
		width:45px;
		font-size:30px;
		line-height: 45px;
		text-align: center;
		background-color:$red;
	}
	.invest-btn{
		border-radius:0;
		
	}
	.invalid{
		background-color:#b9b9b9;
	}
	.icon-jisuanqi1{
		color:$white;
	}
}

/*Expectation*/
.Expectation{width:100%;height:100%;position:fixed;left:0;top:0;background:rgba(0,0,0,.5);z-index:22;display:none;}
.ExpeBox{width:300px;position:absolute;left:50%;top:30%;background:#fff;margin-left:-150px;text-align:center;padding:20px;border-radius:10px}
.ExpeBox p{font-size:17px;color:#000000;margin-bottom:5px}
.money{width:100%;height:40px;border:1px solid #ececec;position:relative;font-size:13px;}
.money input{border:none;width:100%;height:100%;box-sizing:border-box;padding-right:30px;}
.money span{position:absolute;right:10px;top:12px;}
.profitLt{width:100%;margin-top:10px;}
.profit p{font-size:12px;color:#000;float:left;}
.profit em{font-size:12px;color:#000;display:block;}
.profit i{font-size:24px;color:#fb7230;float: right;}
.cancel{width:30px;height:30px;background-image:url(#{$imgPath}/icon-close.png);background-repeat:no-repeat;background-size:30px 30px;background-position:center;position:absolute;right:-10px;top:-13px;}