.certification{
	margin-top:60px;
	.current-steps{
		width:100%;
		height:auto;
		margin-bottom:40px;
		img{
			width:100%;
		}
	}
	.tips{
		color:$certification-tips-color;
		font-size:$f10;
		padding:5px 2px;
		border:1px solid $certification-tips-color;
		margin-bottom:30px;
		text-align: center;
	}

	.guard{
		color:$guard-color;
		font-size:$f10;
		text-align: center;
		margin-top:20px;
		margin-bottom:20px;
	}

	.BBL-xinlang{
		width:204px;
		height:44px;
		background-image:url(#{$imgPath}/BBL-xinlang.png);
		background-size:100% 100%;
		margin: 0 auto;
	}

}

.certification-step2{
	.tips{
		margin-bottom: 0;
	}
}
.error-box{
	height:30px;
	.error-content{
		color:$red;
		font-size:10px;
		margin:0 auto;
	}
}


.reg-input{
	.identifying-code{
		input{
			width:50%;
			float: left;
			padding-left:10px;
		}
		a.get-code{

			float:right;
			color:#6d6d6d;
			line-height: 30px;
			display: block;
			text-align: left;

		}

		a.get{
			color:#e52020;
		}
	}

	li.verify-code .tip-box{
		margin-left:3.5%;
	}
}


.bind-bank{
	margin-bottom:20px;
	ul{
		border-top:1px solid $bind-bank-border-color;
		li{
			
			font-size:14px;
			color:$bindBank-color;
			border-bottom:1px solid $bind-bank-border-color;
			.user-chose{
				height:40px;
				line-height: 40px;
				span.more{
					width:40px;
					height:100%;
					float: right;
					text-align: right;
					line-height: 40px;
					padding-top:2px;
				}
				border-bottom:1px solid $bind-bank-border-color;
				margin-bottom:-1px;
			}
			



			.bank-list{
				dl{
					dd{
						width:100%;
						border-bottom:1px solid $bind-bank-border-color;
						font-size:12px;
						height:46px;
						box-sizing:border-box;
						line-height:36px;
						
						.bankDetails{
							line-height:18px;
							display:block;
							float:left;
						}
						.bankname{
							font-size:13px;
							color:$black;
						}
						.requirement{
							color:#666666;
							font-size:11px;
						}
					}
				}
			}


			.list{
				dl{
					dd{
						padding-left:$f12;
						height: 40px;
						line-height: 40px;
						font-size:$f12;
						color:$bindBank-color;
						border-bottom:1px solid $bind-bank-border-color;
					}
				}
			}


			input{
				border:none;
				outline: none;
				width:50%;
				color:$bindBank-color;
			}

			.get-code{
				float: right;
				line-height: 40px;
				color:$red;
				font-size:$f11;
			}
		}
	}

}

.certification-next-step{
	margin-bottom:20px;
}