.freshmen-experience{
	margin-top:40px;
	margin-bottom:45px;
	ul li{
		display: block;
		background-color:$white;

		padding:0 18px;
		width:100%;
		max-width:640px;
		margin:0 auto;

		span:nth-child(1){
			font-size:$f13;
			color:#2d2d2d;
			width:30%;
			display: inline-block;
		}
		span:nth-child(2){
			font-size:$f12;
			color:#666666;
		}
		line-height: 4.5rem;
		border-bottom: 1px solid #f0f0f0;

		.assets-value{
			padding:$f10 0;
			line-height:3rem;
			span{
				display: inline-block;
				padding:0;
				border:1px solid #666;
				color:#666666;
				text-align: center;
				width:20%;
				border-radius:5px;
			}
			.select{
				background-color:$red;
				border:none;
				color:$white;
			}
		}
	}

}


.confirm-experience{
	position:fixed;
	bottom:0;
	border-radius:0;
	left:50%;


	-webkit-transform:translateX(-50%);
	-moz-transform:translateX(-50%);
	-ms-transform:translateX(-50%);
	-o-transform:translateX(-50%);
	transform:translateX(-50%);
}