.user-projects{
	margin-top:85px;
	.projects{

		li{
			display: block;
			border-top: 1px solid #eeeeee;
			border-bottom: 1px solid #eeeeee;
			background-color:$white;
			margin-bottom:0.5rem;
			.project-detail{
				font-size:$f13;
				padding-bottom:$f12;
				padding-top:$f10;
				color:#5a5a5a;
				.project-name{
					font-size:$f15;
					padding:$f12 0;
					color:#373737;
				}
				.project-item{
					padding:0.5rem 0;
				}
				.invest-money, .project-expect{
					font-size:$f13;
					i{
						font-size:$f11;
					}
					span {
						color:$red;
					}
				}
			}
			.icon-bangzhu{
				color:$yellow;
			}
		}
		
	}

}