/* 底部导航 */
.footer{
	position:fixed;
	bottom:0;
	left:50%;
	-webkit-transform:translateX(-50%);
	-moz-transform:translateX(-50%);
	-ms-transform:translateX(-50%);
	-o-transform:translateX(-50%);
	transform:translateX(-50%);
	background-color:$white;
	height:50px;

	border-top:1px solid $footer-border-color;
	padding:5px 0;

	div.item{
		flex:1;
		text-align: center;
		color:$footer-font-color;
		.item-icon{
			height:24px;
			line-height: 24px;
			.iconfont{
				font-size:$f18;
			}

		}
		.item-text{
			height:16px;
			line-height: 16px;
			font-size:$f10;
		}
		span {
			display: block;
		}
	}

	div.active a{
		color:$footer-acitve-font-color;
	}

	div.account{
		span{
			display: block;
			.item-icon{
				.iconfont{
					font-size:$f20;
				}
			}
		}
		
	}


}

/* 头部导航 */
.header{
	background-color:$white;
	position:fixed;
	top:0;
	left:50%;
	-webkit-transform:translateX(-50%);
	-moz-transform:translateX(-50%);
	-ms-transform:translateX(-50%);
	-o-transform:translateX(-50%);
	transform:translateX(-50%);
	z-index:100;


	height:40px;
	line-height:40px;

	border-bottom:1px solid $header-borderbottom-color;


	ul{
		width:100%;
		height:100%;
		li{
			display:block;
			float:left;
			height:100%;
			span{
				display: block;
				float: left;
				width:40px;
				height:100%;
				line-height:40px;
				i{
					font-size:20px;
				}
			}
		}
		.back{
			width:25%;
			text-align:left;
			
		}
		.title{
			width:50%;
			font-size:20px;
			text-align:center;
			font-size:$header-title-font;
		}
		.other{
			width:25%;
			span{
				float: right;
				text-align:right;
				i.setup{
					font-size:24px;
				}
			}
			span.all-read{
				color:$red;
				font-size:10px;
				width:100%;
			}
		}
	}

	
}

/* 用户输入 */
.user-inputs{
	display: inline-block;
	width:100%;
	height:auto;
	li{
		display: block;
		width:100%;
		height:60px;
		/* 输入框 */
		div.input-box{
			
			height:30px;
			border-bottom:1px solid $user-input-borderbottom-color;
			label{
				width:30px;
				height:30px;
				display: block;
				line-height: 30px;
				text-align: center;
				float:left;
				.iconfont{
					font-size:$f18;
					color:$red;
				}
			}
			
			input{
				border:none;
				outline: none;
				width:85%;
				height:29px;
				font-size:14px;
			}

		
		}
		/* 信息提示 */
		.tip-box{
			width:100%;
			height:24px;
			line-height: 24px;
			font-size:$f10;
			color:$red;
			text-align:left;
			padding-left:12%;
		}

		
	}
}


/* 选项卡 */
.tabs{
	position:fixed;
	top:40px;
	height:40px;
	line-height:40px;
	background-color:$white;
	border-bottom:1px solid #eeeeee;
	z-index:100;
	left:50%;
	-webkit-transform:translateX(-50%);
	-moz-transform:translateX(-50%);
	-ms-transform:translateX(-50%);
	-o-transform:translateX(-50%);
	transform:translateX(-50%);
	.tab-option{
		-webkit-flex:1;
		flex:1;
		font-size:$f14;
		color:#5a5a5a;
		text-align: center;
		span{
			display:inline-block;
			height:100%;
			padding:0 12px;
			margin-bottom: 3px;
		}
	}
	.select{
		color:$red;
		span{
			border-bottom:1px solid $red;
		}
	}
}


/* 帮助中心选项卡 */
.tabs.help-center-tabs{
	height:auto;
	padding-top:$f10;
	padding-bottom:$f10;
	border-bottom:2px solid #dbdbdb;
	span{
		width:6rem;
		height:6rem;
		background-image: url(#{$imgPath}/help-center.png);
		background-size:24rem 6rem;
		background-repeat:no-repeat;
		
		border:none;
		margin-bottom:0;
		display: block;
		margin:0 auto;
	}
	.tab-option{
		opacity: 0.5;
	}
	.select{
		color:#2d2d2d;
	}
	.select{
		opacity: 1;
	}

	.zcdl span{
		background-position:0 0;
	}
	.cztx span{
		background-position:-6rem 0;
	}
	.tzlc span{
		background-position:-12rem 0;
	}
	.bfj span{
		background-position:-18rem 0;
	}
}
.bblIntro{
	.tab-option{
		// font-size:$f13;
		span{
			padding:0 0px;
		}
	}
}