.notice{
	margin-top:41px;
	li{
		padding:$f13 0;
		margin-bottom: $f10;
		background-color:$white;
		.user-message,.project-remind{
			display: block;
			background-image: url(#{$imgPath}/info.png);
			width:3rem;
			height:3rem;
			background-size:6rem 3rem;
			background-repeat: no-repeat;
			background-position:0 0;
			vertical-align: top;
			margin-right: 5px;
		}
		.project-remind{
			background-position:-3rem 0;
		}

		.user-message::after,.project-remind::after{
			visibility: hidden;
			display: block;
			font-size:0;
			content:" ";
			clear:both;
			height:0;
		}

		.hasNew{
			display: block;
			position:relative;
		}
		.hasNew::before{
			position:absolute;
			width:0.6rem;
			height:0.6rem;
			border-radius:50%;
			top:-0.1rem;
			right:-0.1rem;
			content:"";
			background-color:$red;
		}
		.newMessage{
			.message-title{
				font-size:$f15;
				color:#393939;
				margin-bottom: 0.2rem;
				line-height: 18px;
				.title{
					width:70%;
					display: inline-block;
				}
				.date{
					font-size:$f12;
					color:#b7b6b6;
					line-height: $f15;
				}
			}
			.abstract{
				font-size:$f12;
				color:#b7b6b6;
			}
			.message-pic{
				height: 125px;
				border-radius:5px;
				overflow: hidden;
				margin-bottom:$f10;
				margin-top:0.6rem;
				img{
					width:100%;
				}
			}
		}
	}
	.no-Data{
		color:#c9c9c9;
		font-size:$f10;
		text-align:center;
		margin-bottom:$f20;
	}

	.projectsMessage,.myMessage{
		.newMessage{
			.abstract{
				font-size:$f13;
			}
		}
	}
}

