#home{
	position:absolute;
	top:0;
	left:0;
	right:0;
	bottom:0;
	background-color:$home-bgcolor;

	.home-content{
		width:100%;
		height:100%;
		overflow-y: scroll;
		// margin-bottom:10px;
	}

	/* 轮播图 */
	.banner{
		width:100%;
		img{
			width:100%;
		}
	}

	/* 累计交易额 */
	.trade-volume{
		height:11%;
		background-color: $white;
		margin-bottom:$f10;
		border-bottom:1px solid $home-border-color;
		.accumulate{
			height:30%;
			width:100%;
			color:$red;
			padding-top:0.5rem;
			font-size:$f12;
			.toNext{
				color:$home-goDetail-color;
				i{
					display: inline-block;
					vertical-align: top;
					margin-top: 1px;
					font-size:$f12;
				}
			}
		}
		.total-num{
			height:70%;
			width:100%;
			color:#666;
			font-size:$f12;
			span{
				padding:1px 2px;
				font-size:$f18;
				border:1px solid $home-totalNum-color;
				border-radius:3px;
				margin:0 0px;
			}
		}
	}

	/* 推荐项目 */
	.recommend{
		height:18%;
		background-color:$white;
		border-top:1px solid $home-border-color;
		border-bottom:1px solid $home-border-color;
		margin-bottom:10px;
		.invest-name{
			height:30%;
			font-size:$f12;
			color:$home-investTitle-color;
			border-bottom:1px solid $home-border-color;
			span{
				font-size:$f10;
			}
		}
		.invest-intro{
			height:70%;
			width:100%;
			.rate{
				-webkit-flex:1;
				flex:1;
				height:100%;
				.rate-detail{
					height: 100%;
					width:auto;
					.rate-num{
						p.expect{
							color:$red;
							font-size:$f20;
							span{
								font-size:$f13;
								vertical-align: top;
							}
						}
					}
					.intro-title{
						font-size:$f12;
						color:#6d6d6d;
					}
				}
			}
			.days{
				-webkit-flex:2;
				flex:2;
				height:100%;
				.days-num{
					p.duration{
						font-size:$f14;
						// line-height: 24px;
						color:#6d6d6d;
					}
				}
				.days-title{
					font-size:$f12;
					color:#6d6d6d;
				}
			}
			.buy{
				-webkit-flex:2;
				flex:2;
				height:100%;

					.invest-btn{
						height:36px;
						line-height: 36px;
						background-color:$orange;
						font-size:$f18;
					}
			}
		}

	}

	/* 四大功能 */
	.four-features{
		height:23%;
		background-color:$white;
		margin-bottom:10px;
		.feature{
			display: block;
			float: left;
			width:50%;
			.feature-cont{
				height:100%;
				text-align: center;
				align-content:center;
				i{
					color:$red;
					font-size:2.5rem;
				}
				.title{
					font-weight:100;
					font-size:$f10;
					color:#4d4d4d;
				}
				.detail{
					font-size:0.8rem;
					color:#ababab;
				}
			}
			.BBL-logo{
				width:3.5rem;
				height:2.5rem;
				margin:0 auto;
				i{	
					height:$f15;
					width:$f20;
					background-image: url(#{$imgPath}/bbl.png);
					background-size:100% 100%;
					display: block;
					margin:0 auto;
				}
				
			}
		}
		.mission{
			border-right:1px solid #f3f3f3;
			border-bottom:1px solid #f3f3f3;
			margin-right: -1px;
			margin-bottom:-1px;
		}

		.help{
			border-left:1px solid #f3f3f3;
			border-top:1px solid #f3f3f3;
			margin-left: -1px;
			margin-top:-1px;
		}
	}

	/* 平台公告 */
	.notice{
		height:8%;
		background-color: $white;
		font-size:$f13;
		color:#4d4d4d;

		.notice{
			display: block;
			float: left;
			width:3.7rem;
			height:3.6rem;
			background-image: url(#{$imgPath}/notice.png);
			background-size:100% 100%;
			margin-right: 10px;
		}
	}
}