.recharge-withdraw{
	margin-top:58px;
	.recharge-box{
		background-color: $white;
		border-top:1px solid #f1f1f1;
		.icon-renminbi{
			color:$red;
			font-size:$f15;
		}
		ul{
			margin-bottom: 20px;
			li{
				border-bottom:1px solid #f1f1f1;
				padding:0.5rem 0;
				.recharge-money{
					border:none;
					outline: none;
					font-size:$f14;
					margin-left:5px;
				}


				.withdraw-input{
					position:relative;
				}
				.get-all-money{
					position:absolute;
					right:18px;
					background-color:$yellow;
					color:$white;
					border-radius:3px;
					padding:2px;
					display: block;
				}
				@media (max-width:360px){
					.get-all-money{
						right:12px;
					}
				}
				.recharge-attention{
					.recharge-bank,.limit,.fee{
						font-size:$f14;
						color:#505050;
						height:3rem;
						line-height: 3rem;
						margin-bottom:1px;
					}
					.recharge-bank{
						.bank{
							margin:0px;
							display: inline-block;
							vertical-align: middle;
							margin-top: 0.5rem;
						}
					}
					.limit{
						i.icon-renminbi{
							color:#505050;
							font-size:$f13;
						}
					}
					.fee{
						i.icon-renminbi{
							color:#505050;
							font-size:$f13;
						}
					}
				}
			}
		}
	}
	.recharge-explain{
		display: inline-block;
		border-bottom:1px solid #505050;
		color:#505050;
		margin-bottom:30px;
		font-size:$f13;
	}
}

.valid-money{
	margin-top:58px;
	margin-bottom:-48px;
	font-size:$f14;
	color:#505050;
	i{
		font-size:$f13;
	}
}