.infolist{
	margin-top:41px;
	ul{
		li{
			border-bottom:1px solid #eeeeee;
			padding:0.5rem 0;
			position:relative;
			line-height: $f18;
			padding-right:$f10;
			.toNext{
				position:absolute;
				right:0.5rem;
				top:0;
				line-height: 4rem;
				font-size:$f20;
			}
			.info-content{
				position:relative;
				.info-title{
					font-size:$f13;
					color:#393939;
				}
				.info-date{
					font-size:$f11;
					color:#b7b6b6;
				}
			}
			.isNew::before{
				position:absolute;
				left:0.4rem;
				top:0.7rem;
				content: "";
				width:0.5rem;
				height:0.5rem;
				display: block;
				background-color:$red;
				border-radius:50%;
			}
		}
	}
	.no-Data{
		color:#c9c9c9;
		font-size:$f10;
		text-align:center;
		margin-bottom:$f20;
	}
	
}