.discovery-box{
	margin-top:40px;
	.banner{
		img{
			width:100%;
		}
	}
	.options{
		ul{
			width:100%;
			height:auto;
			background-color:$white;

			li{
				// display: block;
				width:100%;
				height:auto;
				border-bottom:1px solid #e6e6e6;
				border-right:1px solid #e6e6e6;
				background-color:$white;
				.option-item{
					-webkit-flex:1;
					flex:1;
					border-right:1px solid #e6e6e6;
					padding:$f15 $f11;
				}
				.option-item:nth-child(2){
					-webkit-flex:1;
					flex:1;
					border-right:none;
				}
				.logo{
					display: block;
					background-color:#ea4c4c;
					width:3rem;
					height:3rem;
					line-height: 3rem;
					text-align:center;
					margin-right:$f10;
					font-size:$f18;
					border-radius:50%;
					color:$white;
				}
				.intro{
					.title{
						font-size: $f13;
						color:#4d4d4d;
						display: block;
						line-height: $f15;
					}
					.abstract{
						font-size:$f10;
						color:#ababab;
						display: block;
						line-height: $f15;
					}
				}
			}
		}
	}


	.latest-news{
		margin-top:$f10;
		border-bottom:1px solid #e6e6e6;
		border-right:1px solid #e6e6e6;
		background-color:$white;
		margin-bottom:40px;

		li{
			font-size:$f12;

			.date{
				font-size: $f10;
				bottom:$f10;
				color:#5a5a5a;
			}
			.abstract{
				line-height: $f18;
			}
		}
	}
}