.invest-filter{
	position:fixed;
	top:40px; 
	left:50%;
	-webkit-transform:translateX(-50%);
	-moz-transform:translateX(-50%);
	-ms-transform:translateX(-50%);
	-o-transform:translateX(-50%);
	transform:translateX(-50%);
	z-index:100;

	.filter-condition{
		.filter{
			-webkit-flex:1;
			flex:1;
			text-align: center;
			height:40px;
			line-height: 40px;
			color:$filter-color;
			font-size:$f14;
			background-color: $white;
		}
	}
	.filter-list{
		position:absolute;
		width:100%;
		height:auto;
		background-color:$white;
		ul li{
			display: block;
			width:100%;
			text-align: center;
			color:$filterList-font-color;
			font-size:$f12;
			padding:5px 0;
		}
		.selected{
			color:$red;
			position:relative;
			.tick{
				position:absolute;
				right:10%;
			}
		}
	}
}

#investList{
	background-color:$filter-bgcolor;
	margin-top:80px;
	// padding-top:50px;
	position:relative;


	.list{
		margin-bottom:50px;
		li{
			background-color:$white;
			position:relative;
			margin-bottom:10px;
			.progress{
				position:absolute;
				left:0;
				top:0;
				width:50%;
				height:100%;
				position:absolute;
				background-color:#fefbeb;
			}
			.list-item{
				position:relative;
				color:$filter-listItem-color;
				.title{
					display: relative;
					font-size:$f12;
					padding-top:5px;
					padding-bottom:5px;
					border-bottom:1px solid $list-bottom-color;
				}
				.detail{
					text-align: center;
					border-bottom:1px solid $list-bottom-color;
					padding-top:5px;
					padding-bottom:5px;
					div:nth-child(2){
						font-size:$f11;
					}
					.rate{
						-webkit-flex:1;
						flex:1;
						.rate-num{
							font-size:$f14;
							line-height: $f20;
							color:$red;
							span{
								font-size:$f10;
								vertical-align: top;
								line-height: $f10;
							}
						}
					}
					.duration{
						-webkit-flex:1;
						flex:1;
						.days{
							font-size:$f12;
							line-height: $f20;
							height:$f20;
						}
					}
					.money{
						-webkit-flex:1;
						flex:1;
						border:none;
						.total{
							font-size:$f13;
							line-height: $f20;
						}
					}
					.state{
						-webkit-flex:1;
						flex:1;
						.btn{
							width:100%;
							height:$f20;
							font-size:$f12;
							line-height: $f20;

							border-radius:$f20 0 0 $f20;
						}
						.state-getReady{
							background-color: $yellow;
						}
						.state-paying{
							background-color:$white;
							color:$black;
						}
						.state-finish{
							background-color:#979797;
						}
					}
				}
				.item-footer{
					padding-top:5px;
					padding-bottom:5px;
					font-size:$f10;
				}

				
			}
			
		}
	}
}