.icon-shezhi{
	color:$orange;
}

.user-account{
	padding-top:40px;
	height:auto;
	background-color:$account-bgcolor;
	.myData{
		position:relative;
		height:auto;
		img{
			width:100%;
			font-size:0px;
		}
		.account{
			position:absolute;
			width: 100%;
			height:100%;
			top:0;
			left:0;
			ul{
				width:100%;
				height:100%;
				padding-top: $f15;
				li{
					width:100%;
					color:$white;
					text-align: center;
				}
				.accumulate{
					font-size:$f12;
					height:20%;
				}
				.all-income{
					font-size:$f20;
					height:20%;
				}
				.pig{
					font-size:$f13;
					height:40%;
				}
				.total{
					font-size:$f12;
					
					.all-assets{
						width:50%;
						text-align: center;
						a{
							color:$white;
							display: inline-block;
						}
					}
					.all-money{
						width:50%;
						text-align: center;
					}
				}
			}
		}
	}
	.money-operate{
		height:41px;
		.btn{
			width:50%;
			background-color: $white;
			color:$red;
			font-size:$f13;
			border-radius: 0;
			border-bottom:1px solid $moneyOperate-border-color;
			border-right:1px solid $moneyOperate-border-color;
		}
		.tixian{
			border-right:none;
		}
		margin-bottom:10px;
	}

	.functions{
		border-top:1px solid $functions-border-color;
		border-bottom:1px solid $functions-border-color;
		.wrap{
			width:100%;
		}
		.item{
			padding:10px 0;
			-webkit-flex:1;
			flex:1;
			text-align: center;
			font-size:$f14;
			color:$functions-font-color;
			border-right:1px solid $moneyOperate-border-color;
			border-top:1px solid $moneyOperate-border-color;
			i{
				display: block;
				font-size:2.5rem;
				color:$red;
				margin-bottom:5px;
			}
		}
		.wrap:nth-child(1){
			.item{
				border-top:none;
			}
		}
		.item:nth-child(3){
			border-right:none;
		}
	}
	
}