.login-box{
	text-align:center;

	.BBL-logo{
		height:200px;
		padding-top: 63px;
		i{
			display: inline-block;
			width:65px;
			height:74px;
			background-image:url(#{$imgPath}/BBL-logo.png);
			background-size:100% 100%;
			margin:0 auto;
		}
	}

	.login-input{
		
		.other-option{
			.go-reg{
				float: left;
				color:$yellow;
			}
			.forget-password{
				float: right;
			}
		}
	}

	.login.inactive{
		background-color:$gray;
	}

}



.reg-protocol{
	margin-top:58px;
}