.reg-box{
	padding-top:40px;
	font-size:12px;
	text-align:center;

	.reg-input{
		margin-top:60px;
		.identifying-code{
			input{
				width:50%;
				float: left;
				padding-left:10px;
			}
			a.get-code{
				float:right;
				color:$reg-getCode-color;
				line-height: 30px;
				display: block;
				text-align: left;
			}

			a.get{
				color:$red;
			}
		}
		li.verify-code .tip-box{
			margin-left:0%;
			padding-left:10px;
		}
	}

	a.next-step{
		color:#fff;
		margin-top:10%;
		margin-bottom:5%;
	}

	p.login{
		font-size:12px;
		text-align: center;
		color:$reg-login-color;
		a{
			color:$orange;
		}
	}

	.setpassword{
		.password2{
			.iconfont{
				font-size:24px;

			}
		}
		.referee{
			.iconfont{
				font-size:16px;
				line-height: 30px;
			}
		}
		.finish-reg{
			color:#fff;
			margin-top:20px;
		}
	}
}



/* 倍倍利协议 */
.protocol{
	text-align:left;
	color:$protocal-color;
	line-height:20px;
	height:40px;
	a{
		color:$red;
	}
	padding-left:10px;

	.tick{
		display: inline-block;
		width:15px;
		height:15px;
		position:relative;
		background-image:url(#{$imgPath}/tick.png);
		background-size:30px 15px;
		background-position: -15px 0;
		vertical-align: top;
		margin-top: 3px;
		margin-right:3px;
		.i-agree{
			position:absolute;
			width:100%;
			height:100%;
			left:0;
			top:0;
			opacity: 0;
		}
	}
	.noSelect{
		background-position: 0 0;
	}
}


.reg-success{
	margin-top:80px;
	text-align:center;
	line-height: 25px;
	.success{
		width:54px;
		height:54px;
		margin:0 auto 20px;
		background-image:url(#{$imgPath}/ok.png);
		background-size:54px 54px;
		background-repeat: no-repeat;
	}
	h5{
		font-size:$f14;
		color:$h5-color;
		font-weight: 500;
		line-height: $f20;
		
	}
	p{
		font-size:$f12;
		color:$h5-color;
		margin-bottom:20px;
	}
}


.reg-protocol{
	margin-top:58px;
	margin-bottom:18px;
	h5{
		font-size:$f14;
		color:$h5-color;
		font-weight: 500;
		text-align: center;
		line-height: $f14;
		margin-bottom:10px;
	}
	ul{
		width:100%;
		height:auto;
		padding-left:38px;
		li{
			list-style-type:decimal;
			font-size:$f12;
			color:$h5-color;
			line-height: $f14;
		}
	}
}