.help-center{

	.tabs{
		position:relative;
		border:none;
	}
	.help-list{
		margin-top:40px;
		border-top:1px solid #dbdbdb;
		ul{
			width:100%;
			height:auto;
			li{
				width:100%;
				border-bottom:1px solid #dbdbdb;
				background-color:$white;
				padding-top:$f10;
				padding-bottom:$f10;

				
				
				.help-detail{
					margin-left:$f13;
					padding-left:$f15;
					padding-right:$f15;

					.problem{
						i{
							color:$red;
							margin-left:-$f18;
						}
						color:#2d2d2d;
						font-size:$f13;
						
					}
					.answer{
						font-size:$f11;
						color:#666666;
						line-height: $f13;
						display: none;
						margin-top: $f10;
						span{
							color:$red;
						}
					}
				}

				.open{
					.answer{
						display: block;
					}
				}
				
			}
		}
	}
}