.investing{
	margin-top:40px;
	ul{
		margin-bottom:20px;
		li{
			width:100%;
			border-bottom:1px solid #e7e5e6;
			height:auto ;
			padding:0.8rem 0;
			font-size:$f14;

			.balance{
				font-size:$f11;
				color:#7d7d7d;
				i{
					font-size:0.6rem;
				}
				.investNow{
					display: inline-block;
					border:1px solid $yellow;
					padding:2px;
					border-radius:2px;
					width:auto;
					height:auto;
					font-size:$f11;
					line-height: $f11;
					background-color:$white;
					color:$yellow;
					vertical-align: center;
					margin-left:2px;
				}
			}
			.purchase{
				padding-top:0.8rem;
				padding-bottom:0.8rem;
				color:#333333;
				.purchase-money{
					border:none;
					width:8rem;
					outline: none;
					height:$f12;
				}
			}
			.expact{
				font-size:$f11;
				color:#878787;
				i{
					font-size:0.6rem;
				}
			}
			.expact:after { 
				visibility:hidden; 
				display:block; 
				font-size:0; 
				content:""; 
				clear:both; 
				height:0;
			}
			.surplus{
				line-height: $f14;
				.surplus-money{
					font-size:$f11;
					i{
						font-size:0.6rem;
					}
				}
			}
			.red-bag{
				line-height: $f14;
				.bag-nums{
					font-size:$f13;
				}
			}
			.valid{
				color:$yellow;
			}
			.invalid{
				color:#b7b7b7;
			}
			.bags{
				padding-top:5px;
				span{
					width:50px;
					height:25px;
					line-height: 25px;
					border:1px solid #e5e5e5;
					border-radius:2px;
					color:#999;
					display: inline-block;
					text-align: center;
					font-size:$f13;
					i{
						font-size:0.5rem;
					}

				}
				.select{
					background-color:$red;
					color:$white;
				}
			}
			.raise-interest{
				line-height: $f14;
				.tickets{
					font-size:$f13;
				}
			}
			.rate-tickets{
				padding-top:5px;
				span{
					width:60px;
					height:35px;
					line-height: 35px;
					
					color:#999;
					display: inline-block;
					text-align: center;
					font-size:$f12;
					background-image:url(#{$imgPath}/tickets.png);
					background-size:120px 35px;
				}
				.select{
					background-position:-60px 0px;
					color:$white;
				}
			}
			.total{
				.total-money{
					font-size:$f13;
					color:$red;
					i{
						font-size:$f11;
					}
				}
			}
		}
	}
}