@charset "utf-8";
 
html{
	font-size:13px;
}
@media (max-height:568px){
	html{
		font-size:10px;
	}
}


 html *{
	padding:0;
	margin:0;
	box-sizing:border-box;
	font-family:"SimHei";
}


body{
	background-color:#eee;
}

a{
	text-decoration:none;
	color:$black;
}


ul,ol{
	list-style:none;
}

.fr{
	float:right;
}

.fl{
	float:left;
}


/* 清除浮动 */
.clear:after{
	clear: both;
	zoom: 1;
	display:block;
	content: '.';
	visibility:hidden;
	height:0;
}

/* 设置宽 */
.full-container{
	width:100%;
	max-width:640px;
	margin:0 auto;
}

/* 设置宽，两边间隙18px */
.container{
	padding:0 18px;
	width:100%;
	max-width:640px;
	margin:0 auto;
}

/* iphone5 以下边距调整为10 */
@media (max-width:360px){
	.container{
		padding:0 12px;
		width:100%;
		max-width:640px;
		margin:0 auto;
	}
}


.hide{
	display:none;
}

/* flex布局 */
.flex{
	display:-webkit-flex;
	display:-ms-flexbox;
	display:flex;
}

.flex-middle{
	display:-webkit-flex;
	display:-ms-flexbox;
	display:flex;

	align-items:center;
}

/* 下一步按钮 */
span.toNext{
	float:right;
	min-width:40px;
	height:100%;
	text-align: right;
}


/* 多余文字处理 */
.surplus{
	overflow: hidden;  
   	white-space: nowrap;  
   	text-overflow: ellipsis; 
}

.h50{
	height:50%;
}
.Pcenter{
	width:100%;
	text-align:center;
}

/* 小图标 */
.tubiao{
	background-image:url(#{$imgPath}/icondanbao.png);
	background-size:72px 54px;
	display:inline-block;
	width:18px;
	height:18px;
	vertical-align:middle;
	margin-top:-3px;
	-webkit-transform:scale(0.8);
	-moz-transform:scale(0.8);
	-ms-transform:scale(0.8);
	-o-transform:scale(0.8);
	transform:scale(0.8);
}

/* 担保 */
.danbao{
	background-position:-18px 0;
}
/* 还本付息 */
.huabenfuxi{
	background-position:0px -36px;
}


.footer-nav{
	position:fixed;
	bottom:0;
	left:0;
	border-top:1px solid #000;
	width:100%;
	height:auto;
	background-color:#58a;
}

.btn{
	display:block;
	border-radius:5px;
	background-color:$red;
	color:$white;
	height: 45px;
	line-height: 45px;
	font-size:18px;
	text-align:center;
	outline:none;
	border:none;
	margin:0 auto;
	width:100%;
}

.btn.inactive{
	background-color:$gray;
}


.red{
	color:$red;
}

/* 银行logo */
.bank{
	display:block;
	width:25px;
	height:25px;
	margin:6px 10px 0 0px;
	background-image:url(#{$imgPath}/bank.png);
	background-repeat:no-repeat;
	background-size:175px 75px;
	text-indent: -9999px;
	float:left;
}


.BOC{background-position:0px 0px;}
.ABC{background-position:-25px 0px;}
.CCB{background-position:-50px 0px;}
.ICBC{background-position:-75px 0px;}
.CITIC{background-position:-100px 0px;}
.CMBC{background-position:-125px 0px;}
.GDB{background-position:-150px 0px;}
.CIB{background-position:0px -25px;}
.CEB{background-position:-25px -25px;}
.BOS{background-position:-50px -25px;}
.PSBC{background-position:-75px -25px;}
.HXB{background-position:-100px -25px;}
.SZPAB{background-position:-125px -25px;}
.SPDB{background-position:-150px -25px;}
.CMB{background-position:0px -50px;}
.BCCB{background-position:-25px -50px;}
.EGBANK{background-position:-50px -50px;}
.CZB{background-position:-75px -50px;}
.CBHB{background-position:-100px -50px;}
.SHRCB{background-position:-125px -50px;}
.COMM{background-position:-150px -50px;}


/* overflow-hidden */
.o-h{
	overflow:hidden;
}

.block {
	display: block !important;
}