.myproject-detail{
	margin-top:45px;
	.project-items{
		background-color: $white;
		color:#5a5a5a;
		font-size:$f13;
		padding-top:0.5rem;
		padding-bottom:$f10;
		margin-bottom:$f10;
		.project-name{
			font-size:$f15;
			padding:$f12 0;
			color:#373737;
		}
		.project-item{
			padding:0.5rem 0;
			.icon-bangzhu{
				color:$yellow;
			}
			.income-money,.invest-moey{
				color:$red;
			}
		}

		.rates{
			.fr{
				span{
					font-size: $f10;
					display: inline-block;
					vertical-align: top;
					margin-right:$f10;
				}
			}
		}
		.iconfont{
			font-size:$f12;
		}
	}

	.pay-plan{
		background-color:$white;
		color:#5a5a5a;
		font-size:$f13;
		padding-top:0.5rem;
		padding-bottom:$f10;
		margin-bottom:$f10;
		li{
			display: block;
			padding:0.5rem 0;
		}
		.title,.invest-detial{
			font-size:$f12;
			div{
				-webkit-flex:3;
				flex:3;
				// text-align:center;
				line-height: $f15;
				height:$f15;
			}
			div:nth-child(1){
				// -webkit-flex:3;
				// flex:2;
			}
			div:nth-child(2){
				-webkit-flex:4;
				flex:4;
			}
			div:nth-child(3){
				-webkit-flex:2;
				flex:2;
			}
			.paying{
				color:$red;
			}
		}
		.invest-detial{
			div:nth-child(2){
				font-size:$f10;
			}
		}

	}
	.invest-contract {
		background-color:$white;
		color:#5a5a5a;
		font-size:$f13;
		padding-top:0.5rem;
		padding-bottom:$f10;
		margin-bottom:$f10;	
	}
}