.welfare{
	margin-top:90px;
	.welfare-item{
		ul{
			width:100%;
		}
		li{
			background-color:$white;
			position:relative;
			display: block;
			color:#717171;
			width:100%;
			box-shadow:0 3px 3px #deddde;
			margin-bottom:$f10;
			.left-pic{
				position:relative;
				width:4.5rem;
				display: inline-block;
				
				.icon-renminbi{
					position:absolute;
					font-size:$f13;
					top:50%;
					right:6%;
					color:#717171;
					-webkit-transform: translateY(-50%);
					-moz-transform: translateY(-50%);
					-ms-transform: translateY(-50%);
					-o-transform: translateY(-50%);
					transform: translateY(-50%);
				}
				img{
					width:100%;
				}
			}
			.use{
				width:84%;
				font-size:$f12;
				border-bottom:1px dashed #eee;
				padding:0.5rem $f10 0.5rem 0;
				.use-money{
					// color:$red;
					font-size:3rem;
				}
				.use-purpose{
					text-align: center;
					line-height: $f18;
					height:3rem;
					span{
						display: block;
					}


				}
			}

			.voucher-from{
				position:absolute;
				width: 84%;
				right:0;
				bottom:0.7rem;
				padding-right:$f10;
				font-size: 0.9rem;
				.voucher-name{
					display: block;
					float: left;
					width:60%;
				}
			}

			.state{
				width:5rem;
				height:4rem;
				background-image:url(#{$imgPath}/welfare-state.png);
				background-size:15rem 4rem;
				background-repeat:no-repeat;
				position:absolute;
				right:11%;
				top:30%;
			}
			.alreay-use{
				background-position:0 0;
			}

			.overdue{
				background-position:-5rem 0;
			}
			.alreay-pay{
				background-position:-10rem 0;
			}

		}
		li.valid{
			.left-pic{
				.icon-renminbi{
					color:$red;
				}
			}
			.use{
				
				.use-money{
					color:$red;
				}
			}
		}
	}

	.rate-voucher{
		li{
			// height:4.5rem;
			.use{
				width:100%;
				padding:0.5rem $f10;
				// border-bottom:1px solid #
				.use-money{
					span{
						font-size:$f12;
					}
				}
			}
			.voucher-from{
				width:100%;
				position:relative;
				right:0;
				bottom:0;
				padding:$f10 $f10;
			}
			.state{
				top:39%;
			}
		}
	}

	.wealth-value{
		li{
			.left-pic{
				width:13%;
			}
			.use{
				.use-money{
					// font-size:$f20;
				}
			}
			.state{
				right:40%;
				top:23%;
			}
		}
	}


}