.investSuccess{
	margin-top:40px;
	padding-top:10px;
	.ok-logo{
		text-align: center;
		font-size:$f14;
		color:#717171;
		padding:20px 0;
		.ok{
			width:12%;
			margin:0 auto;
			margin-bottom:$f15;
			border-radius:50%;
			border:1px solid $yellow;
			font-size:0;
			img{
				width:100%;
			}
		}
	}
	ul{
		margin-bottom:5rem;
		li{
			font-size:$f13;
			color:#5a5a5a;
			padding:0.5rem 0;
			i{
				font-size:$f11;
				vertical-align: center;
				display: inline-block;
				color:$red;
				span{
					font-size:$f13;
				}
			}
		}
	}
	.project-name{
		color:#373737;
		font-size:$f14;
	}
	.invest-money,.income-money{
		color:$red;
	}

	.btns{
		div{
			-webkit-flex:1;
			flex:1;
			.btn{
				height:40px;
				line-height: 40px;
			}
		}
	}
}